import request from "@/utils/request.js";

export function getGoodsList(params) {
    return request({
        url: "/apiadmin/goods/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function loadGoodsAndDetailById(params) {
    return request({
        url: "/apiadmin/goods/goodsdetail",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function editGoodsAndDetail(params) {
    return request({
        url: "/apiadmin/goods/editgoodsdetail",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
export function editGoodsList(params) {
    return request({
        url: "/apiadmin/goods/edit",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
export function deleteGoods(id) {
    return request({
        url: "/apiadmin/goods/delete",
        method: "get",
        params: { id: id }
    });

}
export function loadCateTree() {
    return request({
        url: "/apiadmin/category/gettreemenu",
        method: "get",
    });

}
export function getCategoryList(params) {
    return request({
        url: "/apiadmin/category/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}

export function editCategory(params) {
    return request({
        url: "/apiadmin/category/edit",
        method: "post",
        data: params
    });
}
export function deleteCategoryList(id) {
    return request({
        url: "/apiadmin/category/delete",
        method: "get",
        params: { id: id }
    });

}
export function uploadImage(data) {
    return request({
        url: "/apiadmin/image/upload",
        data: data,
        method: "post",
        headers: {
            //"Content-type": "multipart/form-data",
        }
    })
}
