<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="商品名称"
          size="small"
          style="width: 140px"
          v-model="searchParams.goodsName"
          clearable
        ></el-input>
        <el-cascader
          :options="cateTree"
          size="small"
          @change="handleTypeChange"
          placeholder="类目"
          clearable
        ></el-cascader>

        <el-select
          v-model="searchParams.goodsIsOnSale"
          clearable
          style="width: 140px"
          placeholder="状态"
          size="small"
        >
          <el-option v-for="(k,v,index) in goodsIsOnSaleEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
      <div>
        <el-button type="primary" size="small" @click="add()">添加</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" class="yb-table-expand">
            <el-form-item label="人气推荐">
              <span>{{ goodsIsHotEnum[props.row.goodsIsHot] }}</span>
            </el-form-item>
            <el-form-item label="新品">
              <span>{{ goodsIsNewEnum[props.row.goodsIsNew] }}</span>
            </el-form-item>
            <el-form-item label="创建时间">
              <span>{{ props.row.createTime }}</span>
            </el-form-item>
            <el-form-item label="修改人时间">
              <span>{{ props.row.updateTime }}</span>
            </el-form-item>
            <el-form-item label="图片" style="align-items: center;display: flex;">
              <img :src="props.row.goodsPicUrl" alt srcset width="100" height="100" />
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="goodsSn" label="编号" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsName" label="名称"></el-table-column>
      <el-table-column align="center" prop="categoryName" label="类型" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsUnit" label="单位" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsPrice" label="售价" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsOriginalPrice" label="原价" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsPrice" label="售价" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsPurchasingPrice" label="收购价" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsTransfee" label="运费" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsStock" label="库存" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsSaleNum" label="已售" width="100"></el-table-column>
      <el-table-column align="center" prop="goodsIsOnSale" label="状态" width="110">
        <div slot-scope="s">
          <el-select
            v-model="s.row.goodsIsOnSale"
            placeholder="请选择"
            size="mini"
            @change="changeStatus(s.row)"
          >
            <el-option
              v-for="(k,v,index) in goodsIsOnSaleEnum"
              :key="index"
              :label="k"
              :value="v*1"
            ></el-option>
          </el-select>
        </div>
      </el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">编辑</el-button>
          <el-button type="danger" size="small" @click="removeItem(s.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getGoodsList"
      ref="pagination"
      @returnData="returnData"
    />
  </div>
</template>

<script>
import {
  getGoodsList,
  deleteGoods,
  editGoodsList,
  loadCateTree
} from "@/api/goods/goodsManage";
import {
  GoodsIsOnSaleEnum,
  GoodsIsNewEnum,
  GoodsIsHotEnum
} from "@/utils/Immutable";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
export default {
  data() {
    return {
      searchParams: {
        goodsName: "",
        categoryId: "",
        goodsIsOnSale: "",
        dateRange: []
      },
      pickerOptions: getDateList(),
      getGoodsList: getGoodsList,
      goodsIsOnSaleEnum: GoodsIsOnSaleEnum,
      goodsIsHotEnum: GoodsIsHotEnum,
      goodsIsNewEnum: GoodsIsNewEnum,
      titie: "添加",
      showEditDialog: false,
      cateTree: [],
      tableData: []
    };
  },
  mounted() {
    this.loadCateTree();
  },
  methods: {
    async loadCateTree() {
      const data = await loadCateTree();
      if (data.code != 0) {
        this.$message.error(data.msg);
      }
      this.cateTree = data.data;
    },
    goodsIsOnSaleFormmate(a, b, v) {
      return this.goodsIsOnSaleEnum[v];
    },
    handleTypeChange(v) {
      if (!v) {
        this.searchParams.categoryId = "";
        return;
      }
      this.searchParams.categoryId = v[v.length - 1];
    },
    async changeStatus(v) {
      console.log(v);
      const data = await editGoodsList(v);
      if (data.code != 0) {
        this.$message.error(data.msg);
      }
    },
    edit(row) {
      this.$router.push({ name: "goodsAdd", query: { id: row.id } });
    },
    add() {
      this.$router.push({ name: "goodsAdd" });
    },
    removeItem(item) {
      this.$confirm("此操作将永久删除该配置吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteConfig(item.id).then(res => {
            if (res.code == 0) {
              this.$message.success("删除成功!");
              this.refresh();
              return;
            }
            this.$message.error(res.msg);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    returnData(data) {
      // console.log(data);
      this.tableData = data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    }
  }
};
</script>
<style  scoped>
</style>